import { createRouter, createWebHistory } from 'vue-router'

const routes = [
  {
    path: '/',
    name: 'login',
    component: () => import('@/views/login/login.vue')
  },
  {
    path: '/main',
    name: 'main',
    component: () => import('@/views/layout/index.vue'),
    redirect: '/instrumentPanel',
    children: [
      {
        path: '/instrumentPanel',
        name: '仪表盘',
        component: () => import('@/views/instrumentPanel/instrumentPanel.vue')
      },
      {
        path: '/unorganizedVOC',
        name: 'unorganizedVOC',
        component: () => import('@/views/voc/unorganizedVOC.vue')
      },
      {
        path: '/airCar',
        name: 'airCar',
        component: () => import('@/views/airCar/airCar.vue')
      },
      {
        path: '/wetElectricity',
        name: 'wetElectricity',
        component: () => import('@/views/wetElectricity/wetElectricity.vue')
      },
      {
        path: '/CEMS',
        name: 'CEMS',
        component: () => import('@/views/CEMS/CEMS.vue')
      },
      {
        path: '/apiManagement',
        name: '权限中心/api管理',
        component: () => import('@/views/authorityCenter/apiManagement/apiManagement.vue')
      },
      {
        path: '/roleManagement',
        name: '权限中心/角色管理',
        component: () => import('@/views/authorityCenter/roleManagement/roleManagement.vue')
      },
      {
        path: '/userManagement',
        name: '权限中心/用户管理',
        component: () => import('@/views/authorityCenter/userManagement/userManagement.vue')
      },
      {
        path: '/devicceType',
        name: '配置中心/设备类型',
        component: () => import('@/views/configurationCenter/deviceType/deviceType.vue')
      },
      {
        path: '/equipmentList',
        name: '/配置中心/设备表',
        component: () => import('@/views/configurationCenter/equipmentList/equipmentList.vue')
      },
      {
        path: '/platformParameter',
        name: '/配置中心/平台参数',
        component: () => import("@/views/configurationCenter/platformParameter/platformParameter.vue")
      },

    ]
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  mode:"hash",
  routes
})

router.beforeEach((to, from, next) => {
  let token = localStorage.getItem('loginToken');
  // if (to.path == '/tc') {
  //   localStorage.clear()
  //   next('/')
  // } else if (token==null) {
  //     next('/main')
  // }
  console.log('mc',token);
  if (token) {
    if (to.path == '/') {
      next('/instrumentPanel')
    } else { next() }
  } else {
    if (to.path != "/") {
      next('/')
    } else { next() }
  }
  next()
})

export default router
